import {Component} from '@angular/core'

@Component({
  selector: 'check-form',
  templateUrl: './check.html',
  styles: [`
    .page-request-payment ul.content li {
        background: 0 0;
        line-height: 35px;
    }

    .page-request-payment ul li {
        list-style-type: none;
        border-radius: 0;
        border: none;
        color: #333!important;
        cursor: pointer;
        line-height: 50px;
        font-weight: 500;
        padding-left: 20px;
        padding-right: 20px;
    }

  `]
})
export class CheckFormComponent {}



