import { Component, Output, EventEmitter } from '@angular/core'

@Component({
  selector: 'credit-card-modal',
  templateUrl: './credit-card-modal.html'
})
export class CreditCardModalComponent {
  @Output() creditCard = new EventEmitter()

  submit({card}) {
    console.log(card)
    this.creditCard.next(card)
  }

}



