import {Component} from '@angular/core'

declare const Conekta: any

@Component({
  templateUrl: './bankInformation.html',
  styleUrls: ['./bankInformation.scss']
})
export class BankInformationComponent {

  conektaErrorResponseHandler (response) {
    console.log(response)
  }

  conektaSuccessResponseHandler (response) {
    console.log(response)
  }

  conekta (card) {
    Conekta.setPublishableKey('key_JoUWPMmksPhswjDy')
    Conekta.token.create({card}, this.conektaSuccessResponseHandler, this.conektaErrorResponseHandler)
  }

}



