import {Component, ViewChild} from '@angular/core'
import {AuthService, AccountService, UtilsService} from '../../../services'
import { Observable } from 'rxjs/Observable'
import 'rxjs/add/observable/forkJoin'
import 'rxjs/add/observable/combineLatest'
import 'rxjs/add/operator/map'
import 'rxjs/add/operator/switchMap'
import 'rxjs/add/operator/filter'

@Component({
  templateUrl: './companyProfile.html',
  styleUrls: ['./companyProfile.scss']
})
export class CompanyProfileComponent {
  @ViewChild('companyForm') companyForm

  user$
  account_id
  timezone
  countries
  user_id
  dataFetched
  msg
  show = false

  constructor(
    private auth: AuthService,
    private accountService: AccountService,
    private utilsService: UtilsService
  ) {
    this.user$ = auth.USER
    this.user$.subscribe(data => this.account_id = data.account_id)
    this.getTimeZoneAndCountry()
    this.setCompanyValues()
  }


  getTimeZoneAndCountry () {
    const t = this.utilsService.getTimeZone()
    const c = this.utilsService.getCoutries()

    Observable.forkJoin([t, c])
      .subscribe(([timezone, countries]) => {
        this.timezone = timezone
        this.countries = countries
      })
  }

  setCompanyValues () {
    this.accountService.getCompany(this.account_id)
      .subscribe(data => {
          console.log(data)
          this.dataFetched = data.data
          if (data.data) {
            this.setFormValues(data.account)
          } else {
            this.msg = data.msg
          }
      })
  }

  setFormValues (data) {
    const company = data
    delete company.CreatedAt
    delete company.DeletedAt
    delete company.UpdatedAt
    delete company.active
    delete company.ID
    delete company.logo_url

    this.companyForm.setValue({company})
  }

  submit({company}) {
    this.accountService.updateCompany(this.account_id, company)
      .subscribe((data) => {
        this.dataFetched = data.data
        this.msg = data.msg
      })
  }
}



