import {Component, ViewChild} from '@angular/core'
import {AuthService, AccountService, UtilsService} from '../../../services'


@Component({
  templateUrl: './personalProfile.html',
  styleUrls: ['./personalProfile.scss']
})
export class PersonalProfileComponent {
  @ViewChild('registerForm') registerForm

  user$
  user_id
  dataFetched
  msg
  show = false

  constructor(
    private auth: AuthService,
    private accountService: AccountService,
    private utilsService: UtilsService
  ) {
    this.user$ = auth.USER
    this.user$.subscribe(({user_id}) => this.user_id = user_id)
    this.setUserValues()
  }

  setUserValues () {
    this.accountService.getUser(this.user_id)
      .subscribe(({data, msg, user}) => {
          this.dataFetched = data
          this.msg = msg
          if (data) {
            this.show = data
            delete user.user_id
            this.registerForm.setValue({user})
          }
      })
  }

  submit({user}) {
    const updatedUser = Object.assign({}, user, {user_id: this.user_id})
    delete updatedUser.newPass2

    this.accountService.updateProfile(updatedUser)
      .subscribe(({data, msg}) => {
        this.dataFetched = data
        this.msg = msg
      })
  }
}



