import {NgModule} from '@angular/core'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'
import { NgxDatatableModule } from '@swimlane/ngx-datatable'

import {
  CreateUserComponent,
  UserListComponent,
  AccountMainComponent,
  PersonalProfileComponent,
  CompanyProfileComponent,
  BankInformationComponent,
  PurchaseComponent,
  InvoicesComponent,
  StorageComponent,
  BalanceComponent,
  PaymentRequestsComponent,
  accountRoutes
} from './'

import {UIModule} from '../../ui/ui.module'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    NgxDatatableModule,
    accountRoutes,
    UIModule
  ],
  declarations: [
    CreateUserComponent,
    UserListComponent,
    AccountMainComponent,
    PersonalProfileComponent,
    CompanyProfileComponent,
    BankInformationComponent,
    PurchaseComponent,
    InvoicesComponent,
    StorageComponent,
    BalanceComponent,
    PaymentRequestsComponent
  ]
})
export class AccountModule {
  public static routes = accountRoutes
}



