import {Component} from '@angular/core'

@Component({
  selector: 'modify-storage-modal',
  template: `

  <div class="modal fade" id="modifyStorage" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
          <h4 class="modal-title" id="myModalLabel">Add Bank Account (for receiving payments)</h4>
        </div>

        <div class="modal-content" uib-modal-transclude="" >
          <div class="modal-header" >
            <h3 class="modal-title" > Extra Storage </h3>
          </div >
          <div class="form-group">
            <p> Extra storage increases the amout of files you can upload to your account.
              <br > Event Plans are billed monthly or annually for extra storage.
            </p>
            <select class="form-control">
              <option label="No, Thanks" value="1" selected="selected">No, Thanks</option >
              <option label="Monthly (0.15/GB)" value= "2" > Monthly(0.15 / GB) </option>
              <option label="Annual at 10GB + (0.14/GB a Prepaid)" value="3">Annual at 10GB + (0.14/GB a Prepaid)</option>
              <option label="Annual at 100GB + (0.12 / GB a Prepaid)" value="4">Annual at 100GB + (0.12/GB a Prepaid)</option>
              <option label="Annual at 1000GB + (0.10 / GB a Prepaid)" value="5">Annual at 1000GB + (0.10/GB a Prepaid)</option>
            </select>
          </div>

          <div class="form-group">
            <p> Enter an amount in GB below. This will be pre-purchased and available for 12 months. </p>
            <!-- ngIf: exceed -->
            <label>Prepraid Amount <!-- ngIf: estimation && !exceed -->
              <span class="badge badge- primary ng- binding ng- scope" ng-if="estimation & amp;&amp; !exceed"> 0.00 Usd</span>
              <!-- end ngIf: estimation && !exceed -->
            </label>

            <input type="number" class="form-control">
          </div>

          <div class="modal-footer">
            <button class="btn btn-danger" type="button">Yes</button>
            <button class="btn btn-warning" type="button" >No</button>
          </div>
        </div>

      </div>
    </div>
  </div>
  `
})
export class ModifyStorageModal {}



