import { Component } from '@angular/core'
import {Router} from '@angular/router'
import {Observable} from 'rxjs/Observable'
import { AccountService, AuthService } from '../../../services'

@Component({
  selector: 'user-panel',
  templateUrl: './userList.html',
  styleUrls: ['./userList.scss']
})
export class UserListComponent {

  users$
  user$
  rows: any[]
  editing = {}

  constructor(
    private router: Router,
    private authService: AuthService,
    private accountService: AccountService
  ) {
    this.user$ = authService.USER
    this.user$.subscribe(({account_id}) => {
      this.accountService.getUsers(account_id)
        .subscribe((data) => this.rows = data)
    })
  }

  updateValue(value, cell, row) {
    let update
    switch (cell) {
      case 'role':
        update = Object.assign({}, row, {[cell]: parseInt(value, 10) })
      break
      case 'active':
        update = Object.assign({}, row, {[cell]: (value) ? 1 : 0})
      break
      default:
        update = Object.assign({}, row, {[cell]: value})
    }

    this.user$
      .subscribe(({account_id}) => {
        Object.assign(update, {account_id})
        console.log(update)
        this.accountService.updateValues(update)
          .subscribe(data => {
            this.editing[row.$$index + '-' + cell] = false
            this.rows[row.$$index][cell] = update[cell]
          })
        })
  }
}



