import { Component, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core'
import {Router} from '@angular/router'
import {   AccountService, AuthService } from '../../../services'

@Component({
  selector: 'create-user',
  templateUrl: './createUser.html',
  styleUrls: ['./createUser.scss']
})
export class CreateUserComponent {

  user
  account_id

  constructor(
    private accountService: AccountService,
    private authService: AuthService,
    private router: Router
  ) {
    this.user = this.authService.USER
    this.user.subscribe(({account_id}) => this.account_id = account_id)
  }

  createUser (user) {
    const extraParams = {
      role: parseInt(user.role, 10),
      account_id: this.account_id,
      active: 1
    }
    const updatedUser = Object.assign({}, user , extraParams)

    this.accountService.createUser(updatedUser)
      .subscribe(() => this.router.navigate(['','dashboard', 'account', 'list']))
  }
}



